.section.section-school-diverse {
  padding-top: 90px;
  @media (min-width: 1200px) {
    padding-top: 170px;
  }
  .school-diverse_container {
    position: relative;
    padding-top: 100px;
    padding-bottom: 100px;
    @media (max-width: 991px) {
      padding-top: 0;
      padding-bottom: 0;
    }
    .school-diverse_infor {
      position: relative;
      z-index: 1;
    }
    > .image {
      z-index: 0;
      position: absolute;
      img {
        max-width: 100%;
      }
      @media (max-width: 991px) {
        position: relative;
      }
      &.image-top-left {
        top: -30px;
        left: -130px;
        @media (max-width: 991px) {
          width: 300px;
          display: inline-block;
          left: 0px;
        }
      }
      &.image-top-right {
        top: 30px;
        right: 130px;
        @media (max-width: 991px) {
          display: none;
        }
      }
      &.image-bottom-right {
        bottom: -80px;
        right: -40px;
        @media (max-width: 991px) {
          width: 300px;
          display: inline-block;
          bottom: 0px;
          left: 50%;
        }
        @media (max-width: 767px) {
          left: 20%;
        }
      }
    }
  }
}