.fancybox-ajax-container {
  .fancybox-ajax-content {
    padding-top: 100px;
    padding-bottom: 100px;
    padding-left: 50px;
    padding-right: 50px;
    @media (max-width: 767px) {
      padding-left: 15px;
      padding-right: 15px;
      padding-top: 80px;
      padding-bottom: 80px;
    }
    .fancybox-content {
      overflow: initial;
      border-radius: 5px;
      min-height: 80%;
      @media (max-width: 767px) {
        padding: 25px;
      }
      .container {
        margin: 0 !important;
        width: 1140px;
      }
      .fancybox-button {
        width: 50px;
        height: 50px;
        top: -60px;
        background-color: $white;
        opacity: 1;
        border-radius: 50%;
      }
    }
  }
  &.page-ajax_inner {
    .page-ajax {
      .section-page-image {
        margin-left: -44px;
        margin-right: -44px;
        width: calc(100% + 88px);
      }
    }
  }
}