.section.section-single {
  padding-top: 0;
  padding-bottom: 0;
  h1 {
    font-size: 64px;
  }
  h2 {
    font-size: 42px;
  }
  .meta-atividade {
    margin-bottom: 25px;
    a {
      display: inline-block;
      color: $white;
      background-color: $yellow;
      font-size: 12px;
      line-height: 30px;
      border-radius: 15px;
      padding: 0 15px;
      font-weight: 400;
      text-decoration: none;
    }
  }
  .authors {
    margin-bottom: 50px;
    margin-top: 15px;
    p {
      margin: 0;
      font-size: 14px;
      padding-left: 35px;
      position: relative;
      .icon {
        position: absolute;
        top: 2px;
        left: 0;
      }
    }
  }
  .wp-block-image {
    margin-top: 30px;
  }
  .content {
    audio {
      width: 100%;
    }
  }
}