.page {
  .page-title {
    margin-top: 20px;
    margin-bottom: 30px;
    h1 {
      font-size: 72px;
      @media (max-width: 1199px) {
        font-size: 62px;
      }
      @media (max-width: 767px) {
        font-size: 54px;
        > br {
          display: none;
        }
      }
    }
  }
  .page-file {
    padding-top: 50px;
    padding-bottom: 50px;
    &.page-file_center {
      text-align: center;
    }
    .page-file_title {
      margin-bottom: 40px;
      h3 {
        font-size: 48px;
        line-height: 53px;
        color: $white;
      }
    }
    .page-file_button {
      .btn-download {
        padding: 0 45px;
        font-weight: 500;
        font-size: 16px;
        line-height: 54px;
        height: 58px;
        border: 2px solid $white;
        background-color: transparent;
        border-radius: 27px;
        > i {
          font-size: 28px;
          margin-right: 10px;
          font-weight: lighter;
        }
        > i,
        > span {
          vertical-align: middle;
        }
        &.outline {
          margin-top: 15px;
          color: $orange;
          background-color: $white;
        }
      }
    }
  }
}