.ajax-loading {
  position: relative;
  &::before,
  &::after {
    content: '';
    position: absolute;
  }
  &::before {
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgba($color: #fff, $alpha: .7);
    z-index: 1;
  }
  &::after {
    position: absolute;
    right: calc(50% - 20px);
    top: calc(50% - 20px);
    background: none !important;
    border: 5px solid #CCC;
    animation-name: spin;
    animation-duration: 2000ms;
    animation-iteration-count: infinite;
    animation-timing-function: linear;
    border-bottom-color: #25bdad;
    width: 40px;
    height: 40px;
    border-radius: 50%;
    z-index: 2;
  }
}

#payment-ajax_loading{
  z-index: 99;
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  &::before {
    content: '';
    background-color: #1e1e1e;
    opacity: .9;
  }
  .center {
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    .content {
      z-index: 1;
    }
    h3 {
      color: #fff;
      z-index: 1;
      font-weight: bold;
      margin-top: 120px;
      font-size: 22px;
      letter-spacing: 1px;
      display: inline-block;
      width: 100%;
      text-align: center;
    }
    .success {
      z-index: 1;
      text-align: center;
      margin-top: 15px;
      color: #fff;
      font-size: 16px;
      display: inline-block;
      width: 100%;
    }
  }
}

@-ms-keyframes spin {
  from { -ms-transform: rotate(0deg); }
  to { -ms-transform: rotate(360deg); }
}
@-moz-keyframes spin {
  from { -moz-transform: rotate(0deg); }
  to { -moz-transform: rotate(360deg); }
}
@-webkit-keyframes spin {
  from { -webkit-transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); }
}
@keyframes spin {
  from {
      transform:rotate(0deg);
  }
  to {
      transform:rotate(360deg);
  }
}