.section.section-depoiments {
  position: relative;
  .depoiments_slider {
    margin-left: -5px;
    margin-right: -5px;
    margin-top: 40px;
    padding-bottom: 60px;
    position: relative;
    .column {
      padding: 0 5px;
      height: 100%;
    }
    .btn-slick {
      position: absolute;
      bottom: 0;
      top: initial;
      width: 45px;
      height: 45px;
      border: 2px solid $blue;;
      background-color: $white;
      border-radius: 50%;
      z-index: 1;
      transition: all .25s ease-in-out;
      background-position: center;
      background-repeat: no-repeat;
      font-size: 16px;
      line-height: 46px;
      color: $blue;
      &.slick-prev {
        left: calc(50% - 50px);
        transform: translate(0, 0);
      }
      &.slick-next {
        right: calc(50% - 50px);
        transform: translate(0, 0);
        
      }
      &.slick-disabled {
        background-color: $white;
        border-color: #CFE5E5;
        color: #CFE5E5;
        &.slick-next {
          transform: rotate(180deg);
        }
        &.slick-prev {
          transform: rotate(00deg);
        }
      }
      &:not(.slick-disabled):hover {
        background-color: $blue;
        color: $white;
      }
    }
  }
  .depoiments-item{
    width: 100%;
    border: 2px solid #CFE5E5;
    height: 100%;
    border-radius: 5px;
    .depoiments-item_inner{
      padding: 50px;
      position: relative;
      padding-bottom: 125px;
      height: 100%;
      @media (max-width: 767px) {
        padding: 25px;
        padding-bottom: 125px;
      }
      .inner-row{
        display: flex;
        .left-content {
          width: 75px;
          flex: 0 0 75px;
          max-width: 75px;
          margin-right: 20px;
          @media (max-width: 767px) {
            &.icone{
              display: none;
            }
          }
        }
        .content{
          width: calc(100% - 95px);
          overflow: hidden;
          text-overflow: ellipsis;
          display: -webkit-box;
          -webkit-line-clamp: 4;
          -webkit-box-orient: vertical;
          @media (max-width: 767px) {
            width: 100%;
          }
          p {
            margin-top: 0;
          }
        }
      }
      .depoiments-item_content {
        margin-bottom: 25px;
        .icone {
          height: 45px;
          .medium {
            width: 40px;
            margin-right:5px;
            display: inline-block;
          }
          .small {
            width: 24px;
            transform: rotate(-180deg);
            position: relative;
            top: 5px;
            display: inline-block;
          }
        }
      }
      .depoiments-item_author {
        align-items: center;
        position: absolute;
        bottom: 0;
        left: 0px;
        right: 0px;
        padding: 50px;
        padding-top: 0;
        @media (max-width: 767px) {
          padding: 25px;
          padding-top: 0;
        }
        .avatar-image {
          overflow: hidden;
          display: inline-block;
          height: 75px;
          width: 75px;
          border-radius: 50%;
          position: relative;
          background-position: center;
          background-size: cover;
          > img {
            display: none;
          }
        }
        .name{
          h3, h4 {
            font-family: $text;
            line-height: 1.5;
          }
          h3 {
            font-weight: 900;
            font-size: 16px;
            color: $blue;
          }
          h4 {
            font-weight: 400;
            font-size: 14px;
          }
        }
      }
    }
  }
  .ball-top-left {
    position: absolute;
    top: -50%;
    left: -1%;
    z-index: 99;
    width: 25%;
    > svg {
      width: 100%;
      max-width: 534px;
    }
  }
}