.clientes-short {
  .clientes-short_column {
    h2 {
      font-size: 54px;
      margin-top: 5px;
      color: #ced1d1;
      text-transform: uppercase;
    }
  }
  .clientes-list {
    display: flex;
    flex-wrap: wrap;
    .clientes-item {
      padding: 5px;
      flex: 0 0 25%;
      max-width: 25%;
    }
    .clientes-item_box {
      min-height: 140px;
      width: 100%;
      max-width: 100%;
      border: 1px solid rgba($color: #000000, $alpha: 0);
      background-color: rgba($color: #000000, $alpha: 0.1);
      box-shadow: 0px 0px 0px 0 rgba(25,42,70,.0);
      transition:  all .25s ease-in-out;
      &:hover {
        box-shadow: 0px 3px 8px 0 rgba(25,42,70,.15);
        border-color: rgba($color: #000000, $alpha: 0.1);
      }
      img {
        width: 100%;
        filter: grayscale(100%);
      }
    }
  }
}