.box {
  &.box-tool {
    border: 3px solid #CCC;
    border-radius: 5px;
    margin-top: 15px;
    margin-bottom: 15px;
    transition: all .25s ease-in-out;
    transform: translateY(0);
    &:hover {
      transform: translateY(-4px);
      .box-inner .icone {
        > svg {
          transform: scale(1.05);
        }
      }
    }
    .box-inner {
      height: 346px;
      padding: 25px;
      position: relative;
      padding-bottom: 70px;
      display: block;
      text-decoration: none;
      .icone {
        display: block;
        margin: 0 auto;
        position: relative;
        width: 85px;
        height: 82px;
        margin-top: 20px;
        > svg {
          transition: all .25s ease-in-out;
        }
        > img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
      .titulo {
        margin-top: 25px;
        text-align: center;
        h3 {
          font-family: $title;
          font-size: 30px;
        }
      }
      .link {
        position: absolute;
        left: 25px;
        right: 25px;
        bottom: 25px;
        text-align: right;
        > .link-arrow {
          display: inline-block;
          width: 44px;
          height: 44px;
          line-height: 46px;
          text-align: center;
          background-color: #CCC;
          color: $white;
          background-position: center;
          background-repeat: no-repeat;
          border-radius: 50%;
          font-size: 16px;
          cursor: pointer;
          transition: all .25s ease-in-out;
        }
      }
    }
    &.box-purple {
      border-color: $purple;
      .box-inner {
        .icone > svg{
          > path {
            fill: $purple;
          }
        } 
        .titulo {
          color: $purple;
        }
        .link > .link-arrow {
          background-color: $purple;
          &:hover {
            background-color: darken($color: $purple, $amount: 15%);
          }
        }
      }
    }
    &.box-blue {
      border-color: $blue;
      .box-inner {
        .icone > svg > path {
          fill: $blue;
        }
        .titulo {
          color: $blue;
        }
        .link > .link-arrow {
          background-color: $blue;
          &:hover {
            background-color: darken($color: $blue, $amount: 15%);
          }
        }
      }
    }
    &.box-yellow {
      border-color: $yellow;
      .box-inner {
        .icone > svg > path {
          fill: $yellow;
        }
        .titulo {
          color: $yellow;
        }
        .link > .link-arrow {
          background-color: $yellow;
          &:hover {
            background-color: darken($color: $yellow, $amount: 15%);
          }
        }
      }
    }
    &.box-orange {
      border-color: $orange;
      .box-inner {
        .icone > svg > path {
          fill: $orange;
        }
        .titulo {
          color: $orange;
        }
        .link > .link-arrow {
          background-color: $orange;
          &:hover {
            background-color: darken($color: $orange, $amount: 15%);
          }
        }
      }
    }
  }
  &.more-diversity-item {
    border: 3px solid #CCC;
    border-radius: 5px;
    margin-top: 15px;
    margin-bottom: 15px;
    border-color: $orange;
    transition: all .25s ease-in-out;
    transform: translateY(0);
    &:hover {
      transform: translateY(-4px);
      .box-inner .icone {
        > svg {
          transform: scale(1.05);
        }
      }
    }
    .box-inner {
      height: 270px;
      padding: 25px;
      position: relative;
      padding-bottom: 70px;
      .icone {
        display: block;
        margin: 0 auto;
        position: relative;
        width: 85px;
        height: 82px;
        margin-top: 20px;
        > svg {
          transition: all .25s ease-in-out;
          border-radius: 0;
        }
        > img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
        > svg > path {
          fill: $orange;
        }
      }
      .titulo {
        margin-top: 25px;
        text-align: center;
        color: $orange;
        h3 {
          font-family: $title;
          font-size: 24px;
        }
      }
      .link {
        position: absolute;
        left: 25px;
        right: 25px;
        bottom: 25px;
        text-align: right;
        > a {
          display: inline-block;
          width: 44px;
          height: 44px;
          line-height: 46px;
          background-color: $orange;
          color: $white;
          text-align: center;
          background-position: center;
          background-repeat: no-repeat;
          transition: all .25s ease-in-out;
          border-radius: 50%;
          font-size: 16px;
          cursor: pointer;
          &:hover {
            background-color: darken($color: $orange, $amount: 15%);
          }
        }
      }
    }
  }
}