.form-wrap {
  .form-group {
    margin-bottom: 15px;
    label {
      font-size: .8rem;
      margin-bottom: 5px;
      display: inline-block;
      width: 100%;
      color: #3c5d46;
      //color: #8494A7;
    }
  }
  .form-control {
    max-width: 100%;
    width: 100%;
    border: 1px solid rgba($color: #8494A7, $alpha: .5);
    border-radius: 4px;
    height: 42px;
    line-height: 42px;
    padding: 5px 10px;
    color: #54595f;
    font-size: 1rem;
    transition:  all .25s ease-in-out;
    &.error {
      border-color: #e74c3c !important;
    }
    &.number {
      padding-right: 0;
    }
    &:focus{
      border: 1px solid rgba($color: #8494A7, $alpha: 1);
    }
  }
  .form-submit.wizard-submit {
    width: 100%;
    .btn-continue {
      width: 100%;
    }
  }
}
