#page-activities {
  .activities-column{
    margin: 15px 0;
    .activities-item {
      transition: all .25s ease-in-out;
      transform: translateY(0);
      &:hover {
        transform: translateY(-4px);
      }
      .title {
        height: 32px;
        width: 100%;
        display: block;
        position: relative;
        z-index: 1;
        h2 {
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
          display: inline-block;
          max-width: 100%;
        }
        &:hover {
          h2 {
            overflow: initial;
            text-overflow: unset;
            white-space: normal;
            padding-bottom: 10px;
          }
        }
      }
      .excerpt {
        height: 50px;
        overflow: hidden;
        position: relative;
      }
    }
    &.odd {
      .activities-item {
        background-color: $blue;
        border-color: $blue;
        color: $white;
        .title {
          h2 {
            background-color: $blue;
            z-index: 1;
          }
        }
        .btn-card {
          border-color: $white;
          color: $white;
          transition: all .25s ease-in-out;
          &:hover {
            border-color: $blue;
            color: $blue;
            background-color: $white;
          }
        }
      }
    }
    &.even {
      .activities-item {
        border-color: $blue;
        .title {
          h2 { 
            color: $blue;
          }
        }
        .btn-card {
          border-color: $blue;
          color: $blue;
          transition: all .25s ease-in-out;
          &:hover {
            border-color: $white;
            color: $white;
            background-color: $blue;
          }
        }
      }
    }
  }
  .pagination {
    .wp-pagenavi {
      > span, > a {
        border-color: $blue;
        color: $blue;
        opacity: .5;
        transition:  all .25s ease-in-out;
        &:hover {
          opacity: 1;
        }
        &.current {
          border-color: $blue;
          color: $white;
          background-color: $blue;
          opacity: 1;
        }
      }
    }
  }
}