.section.section-contact {
  padding-top: 120px;
  padding-bottom: 160px;
  @media (max-width: 767px) {
    padding-top: 90px;
    padding-bottom: 120px;
  }
  .contact_infor {
    color: $white;
    .icone{
      margin-bottom: 20px;
      .icone-content {
        display: inline-block;
        width: 70px;
        height: 70px;
        border-radius: 50%;
        position: relative;
        background-color: $white;
        > img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
      }
    }
    h2 {
      font-size: 48px;
    }
  }
  .contact_content {
    > p {
      font-size: 18px;
    }
    .contact_content_phone{
      margin-top: 25px;
      p {
        margin: 0;
        strong {
          font-weight: 600;
        }
      }
    }
  }
  .contact_form {
    margin-top: 30px;
    .form-group {
      margin-bottom: 20px;
      label {
        font-size: 12px;
        font-weight: 400;
        color: $white;
        margin-bottom: 5px;
        display: inline-block;
        width: 100%;
      }
      .wpcf7-form-control {
        width: 100%;
        font-size: 16px;
        border-radius: 5px;
        padding: 5px 10px;
        line-height: 28px;
        font-family: $text;
        font-weight: 400;
        border: 2px solid $white;
        background-color: $white !important;
        &.wpcf7-textarea {
          height: 120px;
          resize: none;
        }
        &.wpcf7-not-valid {
          border: 2px solid red;
        }
      }
      .wpcf7-not-valid-tip{
        display: none;
      }
    }
    .form-submit {
      .wpcf7-submit{
        font-weight: 400;
        font-size: 14px;
        color: $white;
        height: 50px;
        border-radius: 25px;
        line-height: 50px;
        display: inline-block;
        padding: 0 70px;
        cursor: pointer;
        background-color: transparent;
        box-shadow: 0px 0px 10px 2px rgba(0, 0, 0, 0);
        transition:  all .25s ease-in-out;
        font-weight: 700;
        border: 2px solid $white;
        &:hover {
          background-color: $white;
          color: $orange;
          //box-shadow: 9px 17px 10px 2px rgba(0,0,0, 0.1);
        }
      }
      > .ajax-loader {
        position: absolute !important;
        top: 37%;
        left: 50%;
        margin: 0;
        background-color: $white;
        transform: translate(-50%, -50%);
        z-index: 99;
        &::before {
          background-color: $orange;
        }
      }
    }
    .wpcf7-response-output {
      z-index: 99;
      position: fixed;
      border: none;
      right: 30px;
      bottom: 30px;
      box-shadow: 5px 5px 10px 2px rgba(0,0,0, 0.3);
      background-color: $white;
      padding: 15px;
      min-width: 120px;
      text-align: center;
      font-size: 16px;
      margin: 0;
      border-radius: 5px;
      border-left: 5px solid #00a0d2;
      cursor: pointer;
    }
    .wpcf7-form{
      .wpcf7-form-control-wrap{
        display: inline-block;
        width: 100%;
      }
      &.invalid, &.unaccepted {
        .wpcf7-response-output{
          border-color: #ffb900;
        }
      }
      &.submitting {
        position: relative;
        &::after {
          content: '';
          position: absolute;
          top: 0px;
          left: 0px;
          right: 0px;
          bottom: 0px;
          background-color: rgba($color: $orange, $alpha: .7);
          z-index: 9;
        }
      }
    }
  }
  .ball-top-right {
    position: absolute;
    right: 0;
    top: -35%;
    z-index: 99;
  }
}