/** 
 * Remove the unit of a length
 * @param {Number} $number - Number to remove unit from
 * @return {Number} - Unitless number
 *
 **/
@function strip-unit($number) {
  @if type-of($number) == 'number' and not unitless($number) {
    @return $number / ($number * 0 + 1);
  }

  @return $number;
}


/** 
 * REM Font 
 */
@function calculate-rem($size) {
  $remSize: strip-unit($size / 16);
  @return #{$remSize}rem;
}

@mixin font-size($size) {
  font-size: $size; //Fallback in px
  font-size: calculate-rem($size);
}



/**
 * REM All Propriedade 
 */
@mixin rem($property, $values...) {
  $max: length($values);
  $pxValues: '';
  $remValues: '';

  @for $i from 1 through $max {
    $value: strip-unit(nth($values, $i));
    $pxValues: #{$pxValues + $value*16}px;

    @if $i < $max {
      $pxValues: #{$pxValues + " "};
    }
  } 

  @for $i from 1 through $max {
    $value: strip-unit(nth($values, $i));
    $remValues: #{$remValues + $value}rem;

    @if $i < $max {
      $remValues: #{$remValues + " "};
    }
  } 
  
  #{$property}: $pxValues;
  #{$property}: $remValues;
}



/**
 * Transition 
 */
@mixin transition($transition...) {
    -moz-transition:    $transition;
    -o-transition:      $transition;
    -webkit-transition: $transition;
    transition:         $transition;
}


/**
 * Transform 
 */
@mixin transform($value...) {
  -webkit-transform: $value;
  -moz-transform:    $value;
  -ms-transform:     $value;
  -o-transform:      $value;
  transform:         $value;
}


@mixin gradient-background() {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#c50034+0,b12025+50,e06900+83,ea9f3a+99 */
    background: #c50034; /* Old browsers */
    background: -moz-linear-gradient(45deg, #c50034 0%, #b12025 50%, #e06900 83%, #ea9f3a 99%); /* FF3.6-15 */
    background: -webkit-linear-gradient(45deg, #c50034 0%,#b12025 50%,#e06900 83%,#ea9f3a 99%); /* Chrome10-25,Safari5.1-6 */
    background: linear-gradient(45deg, #c50034 0%,#b12025 50%,#e06900 83%,#ea9f3a 99%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
    filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#c50034', endColorstr='#ea9f3a',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
}


@mixin gradient-border() {
    /* Permalink - use to edit and share this gradient: http://colorzilla.com/gradient-editor/#c50034+0,b12025+50,e06900+83,ea9f3a+99 */
    border-width: 2px;
    border-style: solid;
    
    border-color: #c50034; /* Old browsers */
    -moz-border-image: -moz-linear-gradient(45deg, #c50034 0%, #b12025 50%, #e06900 83%, #ea9f3a 99%); /* FF3.6-15 */
    -webkit-border-image: -webkit-linear-gradient(45deg, #c50034 0%,#b12025 50%,#e06900 83%,#ea9f3a 99%); /* Chrome10-25,Safari5.1-6 */
    border-image: linear-gradient(45deg, #c50034 0%,#b12025 50%,#e06900 83%,#ea9f3a 99%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
}

@mixin gradient-text() {
  background: #c50034; /* Old browsers */
  background: -moz-linear-gradient(45deg, #c50034 0%, #b12025 50%, #e06900 83%, #ea9f3a 99%); /* FF3.6-15 */
  background: -webkit-linear-gradient(45deg, #c50034 0%,#b12025 50%,#e06900 83%,#ea9f3a 99%); /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(45deg, #c50034 0%,#b12025 50%,#e06900 83%,#ea9f3a 99%); /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#c50034', endColorstr='#ea9f3a',GradientType=1 ); /* IE6-9 fallback on horizontal gradient */
  background-clip: text;
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}

@mixin placeholder {
    &::-webkit-input-placeholder {
        @content;
    }

    &:-moz-placeholder { /* Firefox 18- */
        @content;
    }

    &::-moz-placeholder {  /* Firefox 19+ */
        @content;
    }

    &:-ms-input-placeholder {
        @content;
    }
}