.section.section-support-realization{
  padding-bottom: 150px;
  @media (max-width: 767px) {
    padding-bottom: 120px;
  }
  .support-realization_infor {
    margin-bottom: 25px;
    .btn-title {
      background-color: $purple;
      font-weight: 600;
      font-size: 14px;
      font-family: $text;
      height: 35px;
      line-height: 35px;
      padding: 0 20px;
      border-radius: 17px;
      cursor: text;
    }
  }
  .support-realization_content {
    border: 2px solid rgba($color: $purple, $alpha: .2);
    border-radius: 5px;
    .support-realization_item {
      height: 170px;
      display: flex;
      text-align: center;
      justify-content: center;
      align-items: center;
      &.item__02 {
        border-left: 2px solid rgba($color: $purple, $alpha: .2);
        border-right: 2px solid rgba($color: $purple, $alpha: .2);
        @media (max-width: 575px) {
          border-left: none;
          border-right: none;
          border-top: 2px solid rgba($color: $purple, $alpha: .2);
          border-bottom: 2px solid rgba($color: $purple, $alpha: .2);
        }
      }
      .support-realization_link  {
        img {
          filter: grayscale(100%);
          transition: all .25s ease-in-out;
          opacity: 0.5;
        }
        &:hover {
          img {
            filter: grayscale(0);
            opacity: 1;
          }
        }
      }
    }
  }
}