.section.section-justification {
  padding-bottom: 150px;
  padding-top: 120px;
  @media (max-width: 991px) {
    padding-bottom: 90px;
    padding-top: 90px;
  }
  @media (max-width: 767px) {
    padding-bottom: 40px;
  }
  .justification_infor {
    text-align: center;
    color: $white;
    @media (max-width: 767px) {
      .justification_content {
        p {
          br {
            display: none;
          }
        }
      }
    }
  }
  .justification_statistics {
    margin-top: 50px;
    .justification_statistics-item {
      display: inline-block;
      color: $white;
      margin-top: 20px;
      margin-bottom: 20px;
      width: 100%;
      &:nth-child(odd){
        .item-content{
          margin-right: 30%;
          @media (max-width: 767px) {
            margin-right: 0;
          }
        }
      }
      &:nth-child(even){
        .item-content{
          margin-left: 30%;
          @media (max-width: 767px) {
            margin-left: 0;
          }
        }
      }
      .image {
        position: relative;
        margin-right: 20px;
        .percent {
          position: absolute;
          left: 50%;
          top: 48%;
          transform: translate(-50%, -50%);
        }
      }
      .description {
        p {
          margin-top: 0;
          font-size: 14px;
          strong {
            font-weight: bold;
          }
        }
      }
      .item-content {
        width: 70%;
        display: flex;
        align-items: center;
        &.item-size-large {
          .percent {
            font-size: 56px;
          }
          .image-large {
            width: 150px;
          }
          .description {
            width: calc(100% - 170px);
            @media (max-width: 767px) {
              width: 100%;
            }
          }
        }
        &.item-size-medium {
          .percent {
            font-size: 40px;
          }
          .image-medium {
            width: 110px;
            margin-left: 30px;
          }
          .description {
            width: calc(100% - 160px);
          }
        }
        &.item-size-small {
          .percent {
            font-size: 32px;
          }
          .image-small {
            width: 90px;
            margin-left: 30px;
          }
          .description {
            width: calc(100% - 150px);
          }
        }
        @media (max-width: 767px) {
          width: 100%;
          &.item-size-large,
          &.item-size-medium,
          &.item-size-small,
          &.item-size-small {
            .percent {
              font-size: 40px;
            }
            .image {
              width: 150px !important;
              margin-left: 0px !important;
            }
            .description {
              width: 100%;
              p {
                > br {
                  display: none;
                }
              }
            }
          }
        }
      }
    }
  }
  .justification-files_infor {
    margin-top: 80px;
    margin-bottom: 80px;
    padding-top: 50px;
    text-align: center;
    color: $white;
    position: relative;
    @media (max-width: 767px) {
      margin-top: 30px;
      margin-bottom: 50px;
      padding-top: 30px;
    }
    &::before {
      content: "";
      width: 46px;
      height: 5px;
      top: 0;
      left: 50%;
      transform: translateY(-50%);
      position: absolute;
      background-image: url('../images/dots.png');
      background-position: center;
    }
    p {
      margin: 0;
    }
  }
  .row.custom-margin {
    margin-left: -5px;
    margin-right: -5px;
    > .column.custom-padding {
      padding-left: 5px;
      padding-right: 5px;
    }
  }
  .justification_files-item {
    &.odd {
      margin-top: 50px;
      @media (max-width: 767px) {
        margin-top: 0;
      }
    }
    .item-content {
      padding: 30px;
      height: 270px;
      background-color: rgba($color: #000, $alpha: .1);
      border-radius: 5px;
      @media (max-width: 767px) {
        height: auto;
        margin-bottom: 25px;
      }
    }
    .icone {
      display: inline-block;
      margin-bottom: 30px;
      > a{
        overflow: hidden;
        float: left;
        width: 50px;
        height: 50px;
        font-size: 0;
        background-color: $white;
        border-radius: 50%;
        background-image: url('../images/download.png');
        background-position: center;
        background-repeat: no-repeat;
        transition: all .25s ease-in-out;
        transform: translateY(0px);
        &:hover {
          transform: translateY(-5px);
        }
      }
    }
    .title {
      color: #FFF;
      p {
        margin-top: 0;
        font-weight: 600;
        font-size: 14px;
      }
    }
  }
  .ball-top-right {
    position: absolute;
    right: 0;
    top: -10%;
    z-index: 99;
  }
  .ball-top-left {
    position: absolute;
    top: 25px;
    left: 15%;
    z-index: 99;
  }
  .ball-bottom-right {
    position: absolute;
    bottom: -175px;
    right: 10%;
    z-index: 99;
  }
  .relative-row {
    position: relative;
  }
  .smille-top-left {
    position: absolute;
    left: -25%;
    top: 5%;
  }
  .smille-top-right {
    position: absolute;
    right: -15%;
    top: 70%;
  }
}