.blog-short {
  .blog-short_column {
    h2 {
      font-size: 54px;
      margin-top: 15px;
      color: $white;
    }

  }
  .blog-short_post{
    .cat {
      a {
        color: $sec;
        font-size: 14px;
        font-weight: bold;
      }
      margin-bottom: 5px;
    }
    .title {
      margin-bottom: 15px;
      h3 {
        color: $brand;
        font-size: 26px;
        font-weight: 600;
      }
    }
    .except {
      margin-bottom: 15px;
      p {
        font-size: 16px;
        color: $white;
      }
    }
    .permalink {
      a {
        font-size: 16px;
        color: $white;
        font-weight: bold;
        text-transform: uppercase;
        cursor: pointer;
      }
    }
  }
}