.apoio-institucional_item {
  margin-top: 40px;
  .title {
    margin-bottom: 15px;
    h4 {
      font-family: $text;
      font-size: 18px;
      font-weight: 700;
    }
  }
  .brand-image {
    display: flex;
    width: 100%;
    min-height: 180px;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 30px;
    border: 2px solid rgba(124, 55, 124, 0.1);
  }
  .text {
    p {
      &:first-of-type {
        margin-top: 0;
      }
    }
  }
}