#page-documentaries {
  .section-documentaries_banner {
    margin-bottom: 50px;
    @media (max-width: 767px) {
      padding-top: 0px;
      padding-bottom: 15px;
    }
  }
  .inner-content {
    .inner-title {
      margin-bottom: 40px;
      h2 {
        font-size: 54px;
        @media (max-width: 767px) {
          font-size: 42px;
          > br {
            display: none;
          }
        }
      }
    }
  }
  .section-documentaries_image {
    padding: 0;
    display: inline-block;
  }
}

.documentaries-column {
  &.even{
    padding-top: 15px;
    padding-bottom: 15px;
    @media (min-width: 768px) {
      padding-left: 55px;
      padding-top: 80px;
    }
  }
  &.odd {
    padding-top: 15px;
    padding-bottom: 15px;
    @media (min-width: 768px) {
      padding-right : 55px;
      padding-bottom: 80px;
    }
  }
}

.documentaries-item {
  .video {
    position: relative;
    margin: 0 auto;
    .embed-responsive {
      border-radius: 5px;
      background-color: rgba($color: $purple, $alpha: .75);
      .embed-responsive-item {
        z-index: 9;
        background-position: center;
        background-size: cover;
        cursor: pointer;
        background-color: rgba($color: $purple, $alpha: .5);
        &.playing {
          visibility: hidden;
          z-index: -1;
          opacity: 0;
        }
        &.on-loading {
          .icon-play {
            opacity: 0;
          }
          &::after {
            content: '';
            position: absolute;
            right: calc(50% - 20px);
            top: calc(50% - 20px);
            background: none !important;
            border: 5px solid rgba($color: #FFF, $alpha: .7);
            animation-name: spin;
            animation-duration: 2000ms;
            animation-iteration-count: infinite;
            animation-timing-function: linear;
            border-bottom-color: #FFF;
            width: 40px;
            height: 40px;
            border-radius: 50%;
            z-index: 2;
            transition:  all .25s ease-in-out;
            transition-delay: .5s;
          }
        }
      }
      .icon-play {
        position: absolute;
        width: 72px;
        height: 72px;
        border-radius: 50%;
        z-index: 10;
        border: 2px solid $white;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        background-image: url('../images/icon-play.png');
        background-position: center;
        background-repeat: no-repeat;
        transition:  all .25s ease-in-out;
        opacity: .7;
      }
    }
    &:hover {
      .icon-play {
        opacity: 1;
      }
    }
  }
  .title {
    position: relative;
    padding-right: 145px;
    margin-top: 15px;
    line-height: 50px;
    overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    min-height: 52px;
    h3 {
      font-size: 32px;
      line-height: 140%;
    }
  }
  .btn-download {
    right: 0;
    top: 0;
    text-align: right;
    padding-right: 60px;
    position: absolute;
    background-color: $white;
    color: $purple;
    height: auto;
    line-height: 50px;
    transition:  all .25s ease-in-out;
    opacity: .6;
    font-size: 14px;
    > i {
      right: 0;
      border-radius: 50%;
      position: absolute;
      display: inline-block;
      width: 50px;
      height: 50px;
      vertical-align: middle;
      background-color: $purple;
      background-repeat: no-repeat;
      background-position: center;
      background-image: url("../images/download-white.png");
    }
    &:hover {
      opacity: 1;
    }
  }
  .excerpt{
    height: 75px;
    overflow: hidden;
    position: relative;
    margin-bottom: 15px;
    margin-top: 15px;
    transition:  all .25s ease-in-out;
    overflow: hidden;
    text-overflow: ellipsis;
    position: relative;
    p {
      margin: 0;
    }
    &.open {
      height: auto;
    }
  }
  .btn-permalink {
    transition:  all .25s ease-in-out;
    opacity: .6;
    background-color: $purple;
    color: $white;
    font-size: 12px;
    height: 32px;
    line-height: 32px;
    border-radius: 16px;
    i {
      transition:  all .25s ease-in-out;
      position: relative;
      transform: rotate(0);
      top: 1px;
      left: 2px;
    }
    &:hover {
      opacity: 1;
    }
  }
  &.open {
    .btn-permalink {
      i {
        transform: rotate(180deg);
      }
    }
  }
}

.documentaries-item_destaque.documentaries-item {
  padding-top: 5%;
  padding-bottom: 5%;
  .video { 
    .embed-responsive {
      background-color: rgba($color: #000, $alpha: .5);
      .embed-responsive-item {
        background-image: none !important;
      }
    }
  }
  .documentaries-item_content {
    .title {
      h3 {
        color: $white;
      }
    }
    .content{
      padding: 0 5%;
      p {
        color: $white;
      }
    }
    .btn-download{
      background-color: transparent;
      color: $white;
      opacity: .8;
      > i {
        background-color: $white;
        background-image: url("../images/download-purple.png");
      }
      &:hover {
        opacity: 1;
      }
    }
  }
}