.banner {
  .banner-home_infor {
    margin-top: 20px;
    margin-bottom: 30px;
    h1 {
      text-align: center;
      font-size: 72px;
    }
    p {
      text-align: center;
      span {
        display: inline-block;
        padding: 2px 20px;
        font-size: 14px;
        background-color: #3B3B3A;
        color: $white;
        font-weight: 500;
        border-radius: 15px;
      }
    }
  }
  .banner-section {
    padding-top: 0;
    padding-bottom: 0;
    @media (max-width: 1199px) {
      > .container {
        max-width: 100%;
      }
      .triangle {
        display: none;
      }
    }
    .banner-home_accodrion{
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      @media (max-width: 1199px) {
        flex-direction: column;
        flex-wrap: nowrap;
        margin-left: -15px;
        margin-right: -15px;
      }
      .banner-item {
        //padding-right: 82px;
        padding-right: 130px;
        padding-left: 0px;
        min-height: 200px;
        position: relative;
        @media (max-width: 1199px) {
          max-width: 100%;
          padding-left: 15px !important;
          padding-right: 15px !important;
          min-height: auto;
        }
        .banner-item_content {
          min-height: 100%;
          width: 0;
          padding-top: 100px;
          padding-bottom: 150px;
          transition:  all .25s ease-in-out;
          @media (max-width: 1199px) {
            min-height: auto;
            width: 100% !important;
            padding-top: 90px;
            padding-bottom: 30px;
          }
          .inner-content {
            opacity: 0;
            width: 0;
            overflow: hidden;
            transition:  all .25s ease-in-out;
            padding: 15px;
            height: 270px;
            width: 370px;
            @media (max-width: 1199px) {
              height: 320px;
              width: 100%;
              max-width: 960px;
              display: flex;
              justify-content: left;
              margin: 0 auto;
              padding-top: 20px;
            }
            @media (max-width: 991px) {
              max-width: 720px;
            }
            @media (max-width: 767px) {
              flex-direction: column;
              height: auto;
            }
            h3 {
              font-family: $text;
              color: $white;
              font-size: 18px;
              font-weight: 700;
              transition:  all .25s ease-in-out;
            }
            .video {
              position: relative;
              margin: 0 auto;
              margin-bottom: 30px;
              width: 340px;
              @media (max-width: 1199px) {
                margin-bottom: 0;
                margin: 0;
                margin-right: 30px;
                width: 500px;
                max-width: 75%;
              }
              @media (max-width: 767px) {
                max-width: 100%;
                width: 100%;
                margin-right: 0px;
                margin-bottom: 30px;
              }
              &.ended {
                .video-overlay {
                  opacity: 1;
                  z-index: 9;
                  visibility: visible;
                }
              }
              .video-overlay {
                position: absolute;
                top: 0;
                right: 0;
                left: 0;
                bottom: 0;
                z-index: 0;
                background-color: rgba($color: $colortext, $alpha: .75);
                display: flex;
                justify-content: center;
                align-items: center;
                opacity: 0;
                visibility: hidden;
                transition:  all .25s ease-in-out;
              }
              .embed-responsive {
                border-radius: 5px;
                background-color: rgba($color: $colortext, $alpha: .75);
                z-index: 2;

                .embed-responsive-item {
                  z-index: 9;
                  background-position: center;
                  background-size: cover;
                  background-color: #000;
                  position: absolute;
                  &::after {
                    content: '';
                    position: absolute;
                    right: calc(50% - 20px);
                    top: calc(50% - 20px);
                    background: none !important;
                    border: 5px solid rgba($color: #FFF, $alpha: .7);
                    animation-name: spin;
                    animation-duration: 2000ms;
                    animation-iteration-count: infinite;
                    animation-timing-function: linear;
                    border-bottom-color: #FFF;
                    width: 40px;
                    height: 40px;
                    border-radius: 50%;
                    z-index: 2;
                    transition:  all .25s ease-in-out;
                    transition-delay: .5s;
                  }
                  &.playing {
                    opacity: 0;
                    cursor: pointer;
                  }
                  &.ended {
                    opacity: 1;
                    &::after {
                      display: none;
                    }
                    &.playing {
                      opacity: 0;
                    }
                  }

                }
                .icon-play {
                  position: absolute;
                  width: 72px;
                  height: 72px;
                  border-radius: 50%;
                  z-index: 10;
                  border: 2px solid $white;
                  top: 50%;
                  left: 50%;
                  transform: scale(1) translate(-50%, -50%);
                  background-image: url('../images/icon-play.png');
                  background-position: center;
                  background-repeat: no-repeat;
                  transition:  all .25s ease-in-out;
                }
              }
              &::before {
                border-radius: 5px;
                padding-top: 56.25%;
                display: block;
                content: "";
                width: 100%;
                background-color: rgba($color: $colortext, $alpha: .5);
                position: absolute;
                transform: rotate(-5deg);
                z-index: 1;
                transition:  all .25s ease-in-out;
              }
              &:hover {
                .icon-play {
                  transform: scale(1.1) translate(-50%, -50%);
                }
                &::before{
                  transform: rotate(0deg);
                }
              }
            }
          }
        }
        .banner-item_header{
          cursor: pointer;
          position: absolute;
          bottom: 0;
          top: 0;
          right: 0;
          //width: 82px;
          width: 130px;
          display: flex;
          align-items: flex-end;
          justify-content: center;
          transform: scale(1);
          transition: all .25s ease-in-out;
          @media (max-width: 1199px) {
            width: 100%;
            padding-bottom: 0 !important;
            justify-content: left;
            padding-left: 30px;
            padding-right: 30px;
          }
          .letter {
            font-family: $title;
            font-size: 100px;
            position: relative;
            transform: translateY(0);
            transition: all .25s ease-in-out;
            @media (max-width: 1199px) {
              font-size: 70px;
              line-height: 90px;
              max-width: 960px;
              margin: 0 auto;
              width: 100%;
            }
            @media (max-width: 991px) {
              max-width: 720px;
            }
          }
          &::before, &::after {
            content: "";
            position: absolute;
            top: 0;
            width: 10px;
            bottom: 0;
            z-index: 4;
            transition: all .25s ease-in-out;
          }
          &::before {
            left: 0;
          }
          &::after {
            right: 0;
          }
        }
        @for $i from 1 through 8 {
          &:nth-child(#{$i}) {
            .banner-item_header{
              padding-bottom: (70px + ($i * 5px));
            }
          }
        }
        &:not(.open) {
          &:hover {
            .banner-item_header{
              .letter {
                transform: translateY(-5px);
              }
              &::before {
                left: -9px;
              }
              &::after {
                right: -9px;
              }
            }
          }
          .banner-item_content {
            padding-left: 0 !important;
            width: 0 !important;
            @media (max-width: 1199px) {
              width: 100% !important;
              padding-bottom: 0;
              height: 0;
            }
          }
        }
        &.open {
          padding-left: 85px;
          .banner-item_content {
            @media (max-width: 1199px) {
              padding-top: 30px;
            }
            .inner-content{
              opacity: 1;
              width: 100%;
            }
          }
          .letter {
            opacity: 0;
          }
        }
      }
    }
  }
}

.banner .banner-section .banner-home_accodrion.open .banner-item {
  padding-right: 82px;
  .banner-item_header {
    width: 82px;
  }
}

.banner .banner-section .banner-home_accodrion .banner-item {
  &.bg-purple {
    .banner-item_header {
      &::before, &::after {
        background-color: $purple;
      }
      .letter{
        color: darken($color: $purple, $amount: 15%);
      }
    }
  }
  &.bg-yellow {
    .banner-item_header {
      &::before, &::after {
        background-color: $yellow;
      }
      .letter{
        color: darken($color: $yellow, $amount: 15%);
      }
    }
  }
  &.bg-blue {
    .banner-item_header {
      &::before, &::after {
        background-color: $blue;
      }
      .letter{
        color: darken($color: $blue, $amount: 15%);
      }
    }
  }
  &.bg-orange {
    .banner-item_header {
      &::before, &::after {
        background-color: $orange;
      }
      .letter{
        color: darken($color: $orange, $amount: 15%);
      }
    }
  }
}