#page-ideas {
  .masonry-row {

  }
  .ideas-column{
    .ideas-item{
      margin-bottom: 15px;
      margin-top: 15px;
      border-color: $yellow;
      transition: all .25s ease-in-out;
      transform: translateY(0);
      &:hover {
        transform: translateY(-4px);
      }
      .ideas-item_inner {
        padding: 30px;
        padding-bottom: 50px;
        .smile {
          left: initial;
          right: 30px;
          @media (max-width: 991px) {
            display: none;
          }
        }
        .inner-row{
          text-align: left;
          padding-right: 80px;
          @media (max-width: 991px) {
            padding-right: 0px;
          }
          title {
            h2 {
              font-size: 36px;
            }
          }
        }
      }
      .activities-rel {
        margin-bottom: 25px;
        > a {
          display: inline-block;
          color: $white;
          background-color: $yellow;
          font-size: 12px;
          line-height: 30px;
          border-radius: 15px;
          padding: 0 15px;
          font-weight: 400;
          text-decoration: none;
        }
      }
      .authors {
        margin-bottom: 50px;
        p {
          margin: 0;
          font-size: 14px;
          padding-left: 35px;
          position: relative;
          .icon {
            position: absolute;
            top: 4px;
            left: 0;
          }
        }
      }
      .artifacts-list{
        .artifacts-list_item {
          margin-bottom: 10px;
          &:last-of-type {
            margin-bottom: 0;
          }
          a {
            margin: 0;
            display: block !important;
            text-decoration: none;
            font-weight: 700;
            font-size: 16px;
            color: $yellow;
            opacity: 1;
            transition: all .25s ease-in-out;
            &:hover {
              opacity: 1;
              color: darken($color: $yellow, $amount: 5%);
              span{
                &.artifacts-icon {
                  .artifacts-icon_background {
                    svg {
                      > path {
                        fill: darken($color: $yellow, $amount: 5%);
                      }
                    }
                  }
                }
              }
            }
            > span {
              vertical-align: middle;
              display: inline-block;
              line-height: 22px;
              &.artifacts-icon {
                width: 60px;
                height: 60px;
                background-position: center;
                background-size: contain;
                background-repeat: no-repeat;
                position: relative;
                margin-right: 15px;
                .artifacts-icon_background {
                  position: absolute;
                  left: 0;
                  right: 0;
                  top: 0;
                  bottom: 0;
                  svg {
                    max-width: 100%;
                    max-height: 100%;
                    transition: all .25s ease-in-out;
                    > path {
                      transition: all .25s ease-in-out;
                    }
                  }
                }
                > img {
                  z-index: 1;
                  position: absolute;
                  top: 50%;
                  left: 50%;
                  transform: translate(-50%, -50%);
                  border-radius: 0;
                }
              }
              &.artifacts-text {
                width: calc(100% -80px); 
                small {
                  font-weight: 500;
                  font-size: 14px;
                }
              }
            }
          }
        }
      }
    }
    &.apresentacao {
      .ideas-item_inner {
        background-color: $yellow;
        .activities-rel {
          > a {
            color: $yellow;
            background-color: $white;
          }
        }
        .title h2,
        .authors p,
        .artifacts-list .artifacts-list_item a{
          color: $white;
        }
        .artifacts-list .artifacts-list_item a {
          > span.artifacts-icon {
            background-image: url('../images/white-ball.svg');
            .artifacts-icon_background {
              svg {
                > path {
                  fill: $white;
                }
              }
            }
          }
        }
      }
    }
    &.even.first-of-type {
      .ideas-item {
        margin-top: 80px;
        @media (max-width: 767px) {
          margin-top: 0;
          margin-bottom: 0;
        }
      }
    }
    &.odd.first-of-type {
      .ideas-item {
        margin-top: 0px;
      }
    }
  }
  .pagination {
    .wp-pagenavi {
      > span, > a {
        border-color: $yellow;
        color: $yellow;
        opacity: .5;
        transition:  all .25s ease-in-out;
        &:hover {
          opacity: 1;
        }
        &.current {
          border-color: $yellow;
          color: $white;
          background-color: $yellow;
          opacity: 1;
        }
      }
    }
  }
}