.pagination {
  margin-top: 50px;
  .pagination-center {
    text-align: center;
  }
  .wp-pagenavi {
    > span, > a {
      width: 44px;
      height: 44px;
      border: 2px solid #CCC;
      color: #CCC;
      padding: 0;
      text-align: center;
      line-height: 40px;
      font-size: 14px;
      font-weight: 700;
      display: inline-block;
      border-radius: 50%;
      margin: 0 5px;
      vertical-align: middle;
      i {
        font-weight: 700;
      }
      &.current {
        border-color: #CCC;
        background-color: #CCC;
        color: #FFF;
      }
      &.disabled {
        border: 2px solid #CCC !important;
        color: #CCC !important;
        pointer-events: none;
        background-color: #FFF !important;
      }
      &.previouspostslink {
        margin-right: 20px;
        line-height: 42px;
      }
      &.nextpostslink{
        margin-left: 20px;
        line-height: 42px;
      }
    
    }
  }
}