.card-box {
  border-radius: 5px;
  border: 2px solid #CCC;
  .card-box_inner{
    padding: 50px 35px;
    position: relative;
    .smile {
      position: absolute;
      left: 30px;
      top: 30px;
      z-index: 0;
    }
    .inner-row {
      position: relative;
      z-index: 1;
      text-align: center;
      justify-content: center;
      .icone {
        > span {
          display: inline-block;
          background-position: center;
          background-repeat: no-repeat;
          border-radius: 50%;
          border: 5px solid $white;
        }
        margin-bottom: 25px;
      }
      .title {
        margin-bottom: 20px;
        h2 {
          font-size: 32px;
        }
      }
      .excerpt {
        margin-bottom: 60px;
        p {
          font-size: 16px;
          font-weight: 400;
          margin-top: 0;
        }
      }
      .card-button {
        > .btn {
          width: 235px;
          height: 54px;
          border: 2px solid #CCC;
          font-size: 16px;
          font-weight: 500;
          line-height: 50px;
          border-radius: 25px;
          background-color: transparent;
          color: #CCC;
        }
      }
    }
  }
}