.section-more-diversity{
  &.section{
    padding-top: 170px;
  }
  .more-diversity_infor {
    text-align: center;
  }
  .more-diversity-box_content {
    margin-top: 50px;
  }
  .row.custom-margin {
    margin-left: -5px;
    margin-right: -5px;
    > .column.custom-padding {
      padding-left: 15px;
      padding-right: 15px;
      @media (min-width: 992px) {
        padding-left: 5px;
        padding-right: 5px;
      }
    }
  }
  &.ajax-content {
    .ajax-title {
      position: relative;
      padding-left: 105px;
      margin-bottom: 50px;
      @media (max-width: 767px) {
        padding-left: 0;
      }
      .icone {
        display: block;
        position: absolute;
        width: 85px;
        height: 82px;
        left: 0;
        top: 0;
        @media (max-width: 767px) {
          position: relative;
          margin-bottom: 15px;
        }
        > img {
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
        }
        > svg > path {
          fill: $orange;
        }
      }
      h2 {
        font-size: 54px;
        line-height: 82px;
        @media (max-width: 767px) {
          line-height: normal;
        }
      }
    }
    .ajax-content-list{
      ul {
        margin-top: 0;
        display: inline-block;
        width: 100%;
        li {
          padding-bottom: 30px;
          margin-bottom: 30px;
          border-bottom: 1px solid #E1E1E1;
          &:last-of-type {
            margin-bottom: 0;
            border-bottom: none;
            padding-bottom: 0;
          }
          h4 {
            font-weight: 700;
            font-size: 16px;
            font-family: $text;
          }
          p {
            margin-top: 10px;
            line-height: normal;
            &.link-content {
              margin-top: 20px;
              a {
                color: $orange;
                padding-right: 26px;
                text-decoration: none;
                position: relative;
                line-height: 18px;
                &::after {
                  content: "";
                  width: 18px;
                  height: 18px;
                  right: 2px;
                  top: 0;
                  position: absolute;
                  background-position: center;
                  background-image: url('../images/arrow-llink.png');
                  background-repeat: no-repeat;
                  transition:  all .25s ease-in-out;
                }
                &:hover {
                  background-position: 99% center;
                  &::after {
                    right: 0;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}