.page-content > .diversity-item:last-of-type {
  margin-bottom: 0;
}

.diversity-item {
  margin-bottom: 80px;
  position: relative;
  .diversity-item_inner{
    .diversity-item_number{
      margin-bottom: 20px;
      .number {
        display: inline-block;
        width: 95px;
        height: 95px;
        background-color: rgba($color: $orange, $alpha: .2);
        text-align: center;
        font-size: 64px;
        line-height: 95px;
        border-radius: 50%;
      }
    }

    .diversity-item_subtitulo {
      h3 {
        font-size: 54px;
      }
    }


    &.left {
      padding-right: 30%;
      @media (max-width: 767px) {
        padding-right: 0;
      }
      .diversity-item_image {
        right: -5%;
      }
    }

    &.right {
      padding-left: 30%;
      @media (max-width: 767px) {
        padding-left: 0;
      }
      .diversity-item_image {
        left: -5%;
      }
    }
  }

  .diversity-item_image {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    width: 35%;
    @media (max-width: 767px) {
      display: none;
    }
  }
}