#header {
  position: absolute;
  top: 0;
  width: 100%;
  left: 0;
  right: 0;
  .header-inner-row  {
    margin-left: 0;
    margin-right: 0;
    align-items: center;
    position: relative;
    .ball{
      position: absolute;
      top: 0;
      right: -260px;
    }
  }
  .brand {
    z-index: 1;
  }
  .brand, .nav-container {
    padding-top: 35px;
  }
  .nav-container {
    display: block;
    @media (max-width: 991px) {
      position: absolute;
      right: 0;
      width: 320px;
      z-index: -1;
      opacity: 0;
      transition:  all .25s ease-in-out;
      padding-left: 50px;
      top: 0px;
      border-radius: 0;
      transform: translateX(100%);
      min-height: 100vh;
      max-width: 100%;
      padding-top: 100px;
      .menu-principal-container {
        position: fixed;
        width: calc(100% - 30px);
        top: 0;
        right: 0;
        left: 50px;
        bottom: 0;
        padding-top: 100px;
        background-color: $white;
        &::before {
          content: '';
          width: 100px;
          height: 100vh;
          position: absolute;
          top: 0;
          bottom: 0;
          right: -15px;
          background: $white;
          transform-origin: 0 0;
          transform: skew(5deg);
          left: -65px;
        }
      }
      &::after {
        content: '';
        width: 200%;
        position: fixed;
        background-color: $white;
        height: 100vh;
        left: 100%;
        top: 0;
      }
      &::before {
        content: '';
        width: 100vw;
        position: fixed;
        background-color: rgba($color: #000000, $alpha: .5);
        height: 100vh;
        right: calc(100% - 50px);
        top: 0;
        z-index: -1;
        opacity: 0;
        transition:  all .15s ease-in-out;
      }
    }
  }
  .toggle {
    width: auto;
    height: 56px;
    background: none;
    border: none;
    padding: 10px 15px;
    right: 0;
    margin-top: 27px;
    cursor: pointer;
    z-index: 999;
    text-align: left;
    background-color: #000;
    border-radius: 50%;
    display: block;
    @media (min-width: 992px) {
      display: none;
    }
    .nav-text {
      color: $white;
      margin-top: 0px;
      text-transform: uppercase;
      font-weight: bold;
      letter-spacing: .4px;
      display: inline-block;
      vertical-align: middle;
      transition: all .2s linear;
      display: none;
    }
    .nav-icon {
      width: 26px;
      padding-right: 0;
      background: transparent;
      cursor: pointer;
      border: 0;
      position: relative;
      height: 22px;
      display: inline-block;
      vertical-align: middle;
      &__bar {
        position: absolute;
        display: block;
        top: 10px;
        width: 26px;
        height: 3px;
        background: $white;
        transition: all .2s linear;
        &:before {
          content: '';
          display: block;
          position: absolute;
          left: 0;
          width: 26px;
          height: 3px;
          background: $white;
          transform: translatey(-8px);
          transition: all .2s linear;
        }
        &:after {
          content: '';
          display: block;
          position: absolute;
          left: 0;
          width: 26px;
          height: 3px;
          background: $white;
          transform: translatey(8px);
          transition: all .2s linear;
        }
      }
    }
    &:hover {
      .nav-text {
        color: #FFF !important;
      }
      .nav-icon__bar {
        background-color: #FFF;
        &:before {
          background-color: #FFF;
        }
        &:after {
          background-color: #FFF;
        }
      }
    }
    &.open {
      .nav-text {
        color: #FFF !important;
      }
      .nav-icon__bar {
        background-color: transparent !important;
        &:before {
          background-color: #FFF;
          transform: rotate(45deg);
        }
        &:after {
          background-color: #FFF;
          transform: rotate(-45deg);
        }
      }
    }
  }
  #menu-principal {
    display: flex;
    margin: 0;
    position: relative;
    z-index: 10;
    @media (max-width: 991px) {
      flex-direction: column;
    }
    .menu-item {
      padding: 0 20px;
      position: relative;
      @media (max-width: 991px) {
        text-align: right;
        padding-left: 0;
        padding-right: 20px !important;
      }
      &:last-of-type {
        padding-right: 0;
      }
      &::after {
        content: '';
        width: 10px;
        height: 10px;
        left: 50%;
        position: absolute;
        bottom: -15px;
        transform: translateX(-50%) translateY(10px);
        border-radius: 50%;
        opacity: 0;
        transition:  all .25s ease-in-out;
        @media (max-width: 991px) {
          display: none;
        }
      }
      &:hover {
        &::after {
          transform: translateX(-50%) translateY(0px);
          opacity: 1;
        }
      }
      a {
        font-family: $text;
        font-size: 14px;
        font-weight: 700;
        text-decoration: none;
        color: $colortext;
        line-height: 22px;
        display: inline-block;
        @media (max-width: 991px) {
          font-size: 16px;
          line-height: 3;
          padding-left: 30px;
          position: relative;
          &::after {
            content: '';
            width: 10px;
            height: 10px;
            left: 15px;
            position: absolute;
            top: 50%;
            transform: translateY(-50%);
            border-radius: 50%;
            opacity: 0;
          }
        }
      }
      &.current-menu-item, &.current_page_item, &:hover {
        a::after {
          opacity: 1;
        }
        &::after {
          transform: translateX(-50%) translateY(0px);
          opacity: 1;
        }
        &.documentaries, &.home{
          a {
            color: $purple;
            &::after {
              background-color: $purple;
            }
          }
          &::after {
            background-color: $purple;
          }
        }
        &.activities{
          a {
            color: $blue;
            &::after {
              background-color: $blue;
            }
          }
          &::after {
            background-color: $blue;
          }
        }
        &.ideas{
          a {
            color: $yellow;
            &::after {
              background-color: $yellow;
            }
          }
          &::after {
            background-color: $yellow;
          }
        }

        &.diversity{
          a {
            color: $orange;
            &::after {
              background-color: $orange;
            }
          }
          &::after {
            background-color: $orange;
          }
        }
      }
    }
  }
  &.show {
    position: fixed;
    z-index: 99;
    .nav-container {
      z-index: 99;
      transform: translateX(0);
      opacity: 1;
      &::before {
        opacity: 1;
      }
    }
  }
}