.btn{
  border: none;
  font-size: 1rem;
  color: #FFF;
  height: 42px;
  line-height: 42px;
  text-align: center;
  background-color: #25bdad;
  min-width: 120px;
  max-width: 100%;
  border-radius: 0px;
  transition:  all .25s ease-in-out;
  text-decoration: none;
  display: inline-block;
  cursor: pointer;
  transform: scale(1);
  &.disabled, &:disabled {
    pointer-events: none;
    background-color: #ccc !important;
    &:hover {
      background-color: #ccc;
      transform: scale(1);
    }
  }
  &:hover {
    background-color: darken($color: #25bdad, $amount: 10%);
  }
  &.btn-calculate {
    width: 100%;
    line-height: 38px;
    border: 1px solid #25bdad;
    background-color: #FFF;
    font-weight: bold;
    color: #25bdad;
    margin-top: 30px;
    border-radius: 21px;
    box-shadow: 0px 0px 0px 0 rgba(25,42,70,.0);
    &:hover {
      box-shadow: 0px 3px 8px 0 rgba(25,42,70,.15);
    }
  }
  &.btn-link {
    height: 58px;
    line-height: 58px;
    padding: 0 40px;
    border-radius: 27px;
    font-size: 14px;
    font-weight: 400;
  }
  &.btn-purple {
    color: $white;
    background-color: $purple;
    cursor: pointer;
    &:hover {
      background-color: lighten($color: $purple, $amount: 10%);
    }
    &.btn-purple_outline {
      background-color: $white;
      border: 2px solid $purple;
      color: $purple;
      &:hover {
        background-color: $purple;
        color: $white;
      }
    }
  }
  &.btn-more {
    border: 1px solid #0e4b4f;
    background-color: #fff;
    color: #0e4b4f;
    padding-left: 30px;
    padding-right: 30px;
    line-height: 40px;
    &:hover {
      background-color: #0e4b4f;
      color: #FFF;
    }
  }
  &.btn-banner {
    font-size: 14px;
    line-height: 42px;
    padding-right: 20px;
    padding-left: 20px;
    cursor: pointer;
    position: relative;
    box-shadow: 0px 0px 0px 0 rgba(25,42,70,.0);
    border: none;
    background-color: $brand;
    color: $white;
    &:hover {
      background-color: darken($color: $brand, $amount: 10%);
      cursor: pointer;
      box-shadow: 0px 3px 8px 0 rgba(25,42,70,.15);
    }
  }
  &.btn-save {
    border: none;
    font-size: 20px;
    padding: 0;
    line-height: 38px;
    background-color: transparent;
    cursor: pointer;
    color: #0e4b4f;
    width: 42px;
    min-width: auto;
    border-radius: 50%;
    &:hover {
      color: #25bdad;
      background-color: #f8f8f8;
      cursor: pointer;
    }
    > i {
      position: relative;
      top: 2px;
    }
  }
  &.btn-arrow {
    border: none;
    font-size: 18px;
    padding: 0;
    line-height: 30px;
    background-color: transparent;
    cursor: pointer;
    color: #0e4b4f;
    width: 30px;
    height: 30px;
    min-width: auto;
    border-radius: 50%;
    transform: rotate(0deg);
    &.open {
      transform: rotate(180deg);
    }
    &:hover {
      color: #25bdad;
      background-color: #f8f8f8;
      cursor: pointer;
    }
  }
  &.btn-paypal {
    background-color: #009cde;
    font-weight: bold;
    margin-top: 15px;
    width: 100%;
    border-radius: 21px;
    &:hover {
      transform: scale(1.1);
      background-color: darken($color: #009cde, $amount: 5%);
    }
    > img {
      vertical-align: middle;
      height: 20px;
      position: relative;
      top: -1px;
    }
  }
  &.btn-home {
    border-radius: .267rem;
    box-shadow: 0 3px 6px rgba(0,0,0,0);
    background-color: #FFF;
    border: 1px solid #25bdad;
    color: #25bdad;
    height: 32px;
    font-weight: normal;
    line-height: 30px;
    padding: 0 50px;
    &:hover {
      color: #FFF;
      border-color: lighten($color: #25bdad, $amount: 5%);
      box-shadow: 0 3px 6px rgba(0,0,0,.2);
      background-color: lighten($color: #25bdad, $amount: 5%);
    }
  }
  &.btn-back,
  &.btn-access {
    padding: 0 15px;
    &:hover {
      color: #FFF;
      border-color: lighten($color: #25bdad, $amount: 5%);
      box-shadow: 0 3px 6px rgba(0,0,0,.2);
      background-color: lighten($color: #25bdad, $amount: 5%);
    }
  }

}
.btn-container {
  &.center{
    text-align: center;
  }
}