.section {
  padding-top: 80px;
  padding-bottom: 80px;
  .ball, .smille {
    display: none !important;
    @media (min-width: 992px) {
      display: block !important;
    }
  }
  h1 {
    @media (max-width: 1199px) {
      font-size: 62px;
    }
  }
  h2 {
    font-size: 68px;
    font-weight: 400;
    font-size: 48px;
    @media (max-width: 991px) {
      > br {
        display: none;
      }
    }
  }
  h3 {
    font-size: 54px;
    font-weight: 400;
    @media (max-width: 1199px) {
      font-size: 48px;
    }
  }
  .tool-box_infor {
    margin-top: 15px;
    @media (max-width: 1199px) {
      margin-top: 0;
      margin-bottom: 25px;
    }
  }
  &.section-nopadding {
    padding-top: 0;
    padding-bottom: 0;
  }
  &.section-triangle-content {
    position: relative;

    .triangle {
      position: absolute;
      left: 0;
      right: 0;
      display: inline-block;
      z-index: 9;
      @media (max-width: 1199px) {
        display: none;
      }
      > img {
        width: 110%;
        border-radius: 0;
        float: left;
        max-width: auto;
        
      }
      &.triangle-top {
        top: - 1px;
      }
      &.triangle-bottom {
        bottom: - 1px;
      }
    }
  }
  &.section-page-image {
    padding: 0;
    display: flex;
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    text-align: center;
    justify-content: center;
    .page-image {
      //max-width: 1600px;
      width: 100%;
      background-size: cover;
      img {
        width: 100%;
        border-radius: 0;
        display: block;
        opacity: 0;
      }
    }
  }
  &.section-tool-box {
    position: relative;
    > .ball-left-midle {
      width: 570px;
      position: absolute;
      left: -5%;
      top: -10%;
      > svg {
        width: 100%;
      }
    }
  }

}